.qb-records__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: rgba(255,255,255, .5);
}

.qb-records {
  cursor: pointer;
  background-color: #87CEFA;
  border: outset .25em #00BFFF;
  padding: 1em 3em;
  position: relative;
  border-radius: .25em;
  counter-reset: records-counter;
}

.qb-records::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: inset .25em #00BFFF;
  pointer-events: none;
}

.qb-records__title {
  margin-bottom: 1em;
  text-align: center;
  font-size: 1.2em;
}

.qb-records-row__counter::after {
  counter-increment: records-counter;
  content: counter(records-counter);
}

.qb-records__table th,
.qb-records__table td {
  padding: .25em .5em;
}



