.qb-message__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: rgba(255,255,255, .5);
}

.qb-message {
  cursor: pointer;
  background-color: #87CEFA;
  border: outset .25em #00BFFF;
  padding: 1em 3em;
  position: relative;
  border-radius: .25em;
}

.qb-message::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: inset .25em #00BFFF;
}

.qb-message__title {
  text-align: center;
  font-size: 1.2em;
}

.qb-message__content {
  margin-top: 1em;
}

.qb-message__follow {
  font-style: italic;
  text-align: center;
  margin-top: 2em;
}



